import angular from 'angular'

export default angular
  .module('CollectionsHomeControllerComponentModule', [])
  .controller(
    'CollectionsHomeController',
    function (
      $scope,
      Restangular,
      canvas,
      collectionsContent,
      config,
      placementsService,
      titleService,
      tree
    ) {
      $scope.config = config
      $scope.canvas = canvas
      $scope.collectionsContent = collectionsContent

      $scope.logPromotedPlacement = (placement, location, index) =>
        placementsService.logPromotedPlacement(placement, location, index)

      titleService.setTitle(tree.current.name)
    }
  )
